@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Reddit Mono", monospace;
  font-style: normal;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Press Start 2P", system-ui;
  font-weight: 400;
  font-style: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.icon {
  border-radius: 100%;
  height: 32px;
  width: 32px;
  margin:auto;
  transition: 200ms all;
}

.icon:hover{
  filter: saturate(200%);
  transform: scale(1.1);
}

.icon img {
  height: 60%;
  width: 60%;
  margin: 20%;
}

